'use client';

import { useEffect, useState } from 'react';
import { MapContainer, TileLayer, Marker, Popup, useMapEvents } from 'react-leaflet';
import L from 'leaflet';
import 'leaflet/dist/leaflet.css';
import './map.css';

// Фиксируем иконки маркеров для Leaflet в Next.js
delete L.Icon.Default.prototype._getIconUrl;
L.Icon.Default.mergeOptions({
  iconRetinaUrl: 'https://cdnjs.cloudflare.com/ajax/libs/leaflet/1.9.4/images/marker-icon-2x.png',
  iconUrl: 'https://cdnjs.cloudflare.com/ajax/libs/leaflet/1.9.4/images/marker-icon.png',
  shadowUrl: 'https://cdnjs.cloudflare.com/ajax/libs/leaflet/1.9.4/images/marker-shadow.png',
});

// Компонент для обработки кликов по карте
function MapClickHandler({ onMapClick }) {
  useMapEvents({
    click: (e) => {
      if (onMapClick) {
        onMapClick(e.latlng);
      }
    },
  });
  return null;
}

// Создаем кастомную иконку для новых точек
const newLocationIcon = new L.Icon({
  iconUrl: 'https://raw.githubusercontent.com/pointhi/leaflet-color-markers/master/img/marker-icon-2x-red.png',
  shadowUrl: 'https://cdnjs.cloudflare.com/ajax/libs/leaflet/1.9.4/images/marker-shadow.png',
  iconSize: [25, 41],
  iconAnchor: [12, 41],
  popupAnchor: [1, -34],
  shadowSize: [41, 41]
});

const InteractiveMap = ({ 
  locations = [], 
  onLocationClick = null, 
  onMapClick = null,
  center = [55.7558, 37.6173], // Москва по умолчанию
  zoom = 10,
  height = '400px'
}) => {
  const [isClient, setIsClient] = useState(false);
  const [tempMarker, setTempMarker] = useState(null);

  // Убеждаемся, что карта рендерится только на клиенте
  useEffect(() => {
    setIsClient(true);
  }, []);

  const handleMapClick = (latlng) => {
    setTempMarker(latlng);
    if (onMapClick) {
      onMapClick(latlng);
    }
  };

  // Очищаем временный маркер при изменении локаций
  useEffect(() => {
    setTempMarker(null);
  }, [locations]);

  if (!isClient) {
    return (
      <div 
        style={{ height, backgroundColor: '#f0f0f0' }}
        className="flex items-center justify-center rounded-lg border-2 border-dashed border-gray-300"
      >
        <p className="text-gray-500">Загрузка карты...</p>
      </div>
    );
  }

  return (
    <div style={{ height }} className="rounded-lg overflow-hidden border shadow-md">
      <MapContainer
        center={center}
        zoom={zoom}
        style={{ height: '100%', width: '100%' }}
        className="z-0"
      >
        <TileLayer
          attribution='&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors'
          url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
        />
        
        {/* Обработчик кликов по карте */}
        <MapClickHandler onMapClick={handleMapClick} />
        
        {/* Отображаем существующие локации */}
        {locations.map((location, index) => (
          <Marker
            key={location.id || index}
            position={[Number(location.latitude), Number(location.longitude)]}
          >
            <Popup>
              <div className="p-2">
                <h3 className="font-bold text-lg mb-1">{location.name}</h3>
                {location.description && (
                  <p className="text-gray-600 mb-2">{location.description}</p>
                )}
                <div className="text-sm text-gray-500">
                  <p>Широта: {Number(location.latitude).toFixed(6)}</p>
                  <p>Долгота: {Number(location.longitude).toFixed(6)}</p>
                </div>
                {onLocationClick && (
                  <button
                    onClick={() => onLocationClick(location)}
                    className="mt-2 px-3 py-1 bg-blue-500 text-white rounded text-sm hover:bg-blue-600 transition-colors not-active:opacity-50 cursor-no-drop"
                  >
                    Подробнее
                  </button>
                )}
              </div>
            </Popup>
          </Marker>
        ))}
        
        {/* Временный маркер для новой точки */}
        {tempMarker && (
          <Marker 
            position={[tempMarker.lat, tempMarker.lng]}
            icon={newLocationIcon}
          >
            <Popup>
              <div className="p-2">
                <h3 className="font-bold text-lg mb-1">Новая точка</h3>
                <div className="text-sm text-gray-500">
                  <p>Широта: {Number(tempMarker.lat).toFixed(6)}</p>
                  <p>Долгота: {Number(tempMarker.lng).toFixed(6)}</p>
                </div>
                <p className="text-xs text-gray-400 mt-1">
                  Кликните для добавления локации
                </p>
              </div>
            </Popup>
          </Marker>
        )}
      </MapContainer>
    </div>
  );
};

export default InteractiveMap;