"use client";
import { useState, useEffect } from 'react';

const AddLocationModal = ({ isOpen, onClose, onAdd, selectedLocation = null }) => {
    const [locationData, setLocationData] = useState({
        name: '',
        description: '',
        latitude: '',
        longitude: ''
    });

    // Заполняем координаты при выборе точки на карте
    useEffect(() => {
        if (selectedLocation) {
            setLocationData(prev => ({
                ...prev,
                latitude: selectedLocation.lat.toFixed(6),
                longitude: selectedLocation.lng.toFixed(6)
            }));
        }
    }, [selectedLocation]);

    const handleSubmit = (e) => {
        e.preventDefault();
        if (!locationData.name.trim()) return;

        onAdd({
            ...locationData,
            latitude: parseFloat(locationData.latitude),
            longitude: parseFloat(locationData.longitude),
            id: Date.now(), // Временный ID
            createdAt: new Date().toISOString()
        });

        setLocationData({
            name: '',
            description: '',
            latitude: '',
            longitude: ''
        });
        onClose();
    };

    const handleClose = () => {
        setLocationData({
            name: '',
            description: '',
            latitude: '',
            longitude: ''
        });
        onClose();
    };

    if (!isOpen) return null;

    return (
        <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50">
            <div className="bg-white rounded-lg p-6 w-full max-w-md">
                <div className="flex justify-between items-center mb-4">
                    <h2 className="text-xl font-semibold">Add Location Point</h2>
                    <button
                        onClick={handleClose}
                        className="text-gray-500 hover:text-gray-700"
                    >
                        <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M6 18L18 6M6 6l12 12" />
                        </svg>
                    </button>
                </div>

                {selectedLocation && (
                    <div className="mb-4 p-3 bg-blue-50 rounded-lg">
                        <p className="text-sm text-blue-700">
                            <strong>Selected coordinates:</strong> {selectedLocation.lat.toFixed(6)}, {selectedLocation.lng.toFixed(6)}
                        </p>
                    </div>
                )}

                <form onSubmit={handleSubmit} className="space-y-4">
                    <div>
                        <label className="block text-sm font-medium text-gray-700 mb-1">
                            Point Name *
                        </label>
                        <input
                            type="text"
                            required
                            value={locationData.name}
                            onChange={(e) => setLocationData(prev => ({ ...prev, name: e.target.value }))}
                            className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                            placeholder="e.g., Sample Location 1"
                        />
                    </div>

                    <div>
                        <label className="block text-sm font-medium text-gray-700 mb-1">
                            Description
                        </label>
                        <textarea
                            value={locationData.description}
                            onChange={(e) => setLocationData(prev => ({ ...prev, description: e.target.value }))}
                            className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                            rows="3"
                            placeholder="Description of this location..."
                        />
                    </div>

                    <div className="grid grid-cols-2 gap-4">
                        <div>
                            <label className="block text-sm font-medium text-gray-700 mb-1">
                                Latitude
                            </label>
                            <input
                                type="number"
                                step="any"
                                value={locationData.latitude}
                                onChange={(e) => setLocationData(prev => ({ ...prev, latitude: e.target.value }))}
                                className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                                placeholder="40.7128"
                            />
                        </div>
                        <div>
                            <label className="block text-sm font-medium text-gray-700 mb-1">
                                Longitude
                            </label>
                            <input
                                type="number"
                                step="any"
                                value={locationData.longitude}
                                onChange={(e) => setLocationData(prev => ({ ...prev, longitude: e.target.value }))}
                                className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                                placeholder="-74.0060"
                            />
                        </div>
                    </div>

                    <div className="flex justify-end gap-3 mt-6">
                        <button
                            type="button"
                            onClick={handleClose}
                            className="px-4 py-2 text-sm font-medium text-gray-700 hover:text-gray-900"
                        >
                            Cancel
                        </button>
                        <button
                            type="submit"
                            className="px-4 py-2 bg-blue-600 text-white text-sm font-medium rounded-md hover:bg-blue-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-blue-500"
                        >
                            Add Point
                        </button>
                    </div>
                </form>
            </div>
        </div>
    );
};

export default AddLocationModal;